<template>
  <div class="custom-tabs flex ac">
    <van-tabs
      class="item"
      v-model="active"
      :scrollspy="scrollspy "
      :sticky="sticky"
      :line-width="lineWidth"
      :line-height="lineHeight"
      :color="color"
      :title-active-color="color"
      title-inactive-color="#282828"
      :swipe-threshold="swipeThreshold"
      :offset-top="offsetTop"
      @change="onChange"
    >
      <van-tab v-for="(item, index) in tabs" :title="item.name" :key="index">
        <slot :name="`tab${index}`"></slot>
      </van-tab>
    </van-tabs>
    <div class="custom-tabs-right flex column center">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Tab, Tabs } from "vant";

Vue.use(Tab);
Vue.use(Tabs);
export default {
  props: {
    tabs: {
      // {name: '', value: ''}
      type: Array,
      default: () => []
    },
    // 底部条宽度
    lineWidth: {
      type: String,
      default: "50px"
    },
    // 底部条高度
    lineHeight: {
      type: String,
      default: "1px"
    },
    color: {
      type: String,
      default: "#E84114"
    },
    // 粘性定位布局
    sticky: {
      type: Boolean,
      default: false
    },
    // 滚动导航
    scrollspy: {
      type: Boolean,
      default: false
    },
    swipeThreshold: {
      type: String,
      default: "4"
    },
    offsetTop: {
      type: String,
      default: "0"
    }
  },
  data() {
    return {
      active: 0
    };
  },
  methods: {
    onChange(index) {
      this.$emit("tabChange", index, this.tabs[index].value);
    }
  }
};
</script>

<style lang="less">
.custom-tabs {
  .van-tab {
    padding: 0;
  }
}
.custom-tabs-right {
  height: 44px;
  background: #fff;
}
</style>